import React, { Component } from "react";
import Welcome from "./components/Welcome";
import About from "./components/About";
import Navbar from "./components/Navbar";
import Portfolio from "./components/Portfolio";
import Skills from "./components/Skills";
import Contact from "./components/Contact";

import { frontEndProjects, fullStackProjects } from "./data/project_data.js";
import { library } from "@fortawesome/fontawesome-svg-core";
// eslint-disable-next-line
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { fab } from "@fortawesome/free-brands-svg-icons";
import {
  faEnvelope,
  faArrowRight,
  faArrowLeft,
  faTimes,
  faExternalLinkAlt,
  faExternalLinkSquareAlt,
  faWindowClose,
  faCircle,
  faCode,
  faUser,
  faLaptop,
  faDesktop,
  faTabletAlt,
  faMobileAlt,
  faServer,
  faDatabase
} from "@fortawesome/free-solid-svg-icons";
library.add(
  fab,
  faEnvelope,
  faArrowRight,
  faArrowLeft,
  faTimes,
  faExternalLinkAlt,
  faExternalLinkSquareAlt,
  faWindowClose,
  faCircle,
  faCode,
  faUser,
  faLaptop,
  faDesktop,
  faTabletAlt,
  faMobileAlt,
  faServer,
  faDatabase
);

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      navbarShow: false,
      currentPage: "welcome",
      modalShow: false,
      modalInfo: {
        title: "",
        pic: "",
        desc: "",
        skills: [],
        url: ""
      }
    };
    this.switchAbout = this.switchAbout.bind(this);
    this.switchPortfolio = this.switchPortfolio.bind(this);
    this.switchSkills = this.switchSkills.bind(this);
    this.switchContact = this.switchContact.bind(this);
    this.switchWelcome = this.switchWelcome.bind(this);
    this.handleKeyDown = this.handleKeyDown.bind(this);
    this.openModal = this.openModal.bind(this);
    this.closeModal = this.closeModal.bind(this);
  } // end of constructor

  componentWillMount() {
    document.addEventListener("keydown", this.handleKeyDown.bind(this));
  }

  componentWillUnmount() {
    document.removeEventListener("keydown", this.handleKeyDown.bind(this));
  }

  handleKeyDown(event) {
    // Left Arrow
    if (event.keyCode === 37) {
      switch (this.state.currentPage) {
        case "portfolio":
          this.switchAbout();
          break;
        case "skills":
          this.switchPortfolio();
          break;
        case "contact":
          this.switchSkills();
          break;
        default:
          break;
      }
    } // end of if
    // Right Arrow
    else if (event.keyCode === 39) {
      switch (this.state.currentPage) {
        case "about":
          this.switchPortfolio();
          break;
        case "portfolio":
          this.switchSkills();
          break;
        case "skills":
          this.switchContact();
          break;
        default:
          break;
      }
    } // end else if (right arrow)
    //Enter or space-bar
    else if (event.keyCode === 13 || event.keyCode === 32) {
      switch (this.state.currentPage) {
        case "welcome":
          this.switchAbout("welcome");
          break;
        default:
          break;
      }
    } // end else if (enter or space-bar)
    //Esc key (close modal if on portfolio and modal showing)
    else if (
      event.keyCode === 27 &&
      this.state.currentPage === "portfolio" &&
      this.state.modalShow
    ) {
      this.closeModal();
    }
  }

  //Open Portfolio full card Modal
  openModal(event) {
    const projectName = event.currentTarget.id;
    const type = event.currentTarget.dataset.type;
    let project;

    //Filter by type
    if (type === "front-end") {
      project = frontEndProjects.filter(project => project.title === projectName);
    } else if (type === "full-stack") {
      project = fullStackProjects.filter(project => project.title === projectName);
    }

    this.setState({
      modalShow: true,
      modalInfo: {
        title: project[0].title,
        pics: project[0].pics,
        desc: project[0].desc,
        skills: project[0].skills,
        url: project[0].url
      }
    });
  }

  //Close Portfolio full card Modal
  closeModal() {
    this.setState({
      modalShow: false,
      modalInfo: {
        title: "",
        pic: "",
        desc: "",
        skills: [],
        url: ""
      }
    });
  }

  switchAbout(tag) {
    this.setState({ currentPage: "about" });
    document.body.classList.remove("welcome-body");
    if (tag !== undefined) {
      this.setState({ navbarShow: true });
    }
  }

  switchPortfolio(event) {
    this.setState({ currentPage: "portfolio" });
  }

  switchSkills(event) {
    this.setState({ currentPage: "skills" });
  }

  switchContact(event) {
    this.setState({ currentPage: "contact" });
  }

  switchWelcome(event) {
    this.setState({ currentPage: "welcome", navbarShow: false });
    document.body.classList.add("welcome-body");
  }

  render() {
    return (
      <div id="app-container">
        {this.state.navbarShow && (
          <Navbar
            switchAbout={this.switchAbout}
            switchPortfolio={this.switchPortfolio}
            switchSkills={this.switchSkills}
            switchContact={this.switchContact}
            switchWelcome={this.switchWelcome}
            currentPage={this.state.currentPage}
          />
        )}
        {this.state.currentPage === "welcome" && <Welcome switchAbout={this.switchAbout} />}
        {this.state.currentPage === "about" && <About switchPortfolio={this.switchPortfolio} />}
        {this.state.currentPage === "portfolio" && (
          <Portfolio
            switchAbout={this.switchAbout}
            switchSkills={this.switchSkills}
            modalShow={this.state.modalShow}
            modalInfo={this.state.modalInfo}
            openModal={this.openModal}
            closeModal={this.closeModal}
          />
        )}
        {this.state.currentPage === "skills" && (
          <Skills switchPortfolio={this.switchPortfolio} switchContact={this.switchContact} />
        )}
        {this.state.currentPage === "contact" && <Contact switchSkills={this.switchSkills} />}
      </div>
    );
  }
} // end of App component

export default App;
