import React, { Component } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { frontEndProjects, fullStackProjects, featuredProject } from "../data/project_data.js";

class Portfolio extends Component {
  constructor(props) {
    super(props);
    this.state = {
      currentCat: "featured"
    };
    this.switchCat = this.switchCat.bind(this);
  } // end constructor

  //Switch portfolio category
  switchCat(event) {
    this.setState({ currentCat: event.currentTarget.dataset.value });
  }

  render() {
    return (
      <div id="portfolio-page" className="app-page">
        <div className="page-header animated fadeIn">
          <h1>Primo Portfolio</h1>
        </div>
        <div className="page-content" id="portfolio-content">
          <PortfolioMenu currentCat={this.state.currentCat} switchCat={this.switchCat} />
          {this.state.currentCat === "featured" && <Featured openModal={this.props.openModal} />}
          {this.state.currentCat === "front-end" && <FrontEnd openModal={this.props.openModal} />}
          {this.state.currentCat === "full-stack" && <FullStack openModal={this.props.openModal} />}
          {this.props.modalShow && (
            <Modal modalInfo={this.props.modalInfo} closeModal={this.props.closeModal} />
          )}
        </div>{" "}
        {/* End page-content*/}
        <div className="page-arrows">
          <FontAwesomeIcon
            className="arrow"
            icon={["fas", "arrow-left"]}
            size="2x"
            onClick={this.props.switchAbout}
          />
          <FontAwesomeIcon
            className="arrow"
            icon={["fas", "arrow-right"]}
            size="2x"
            onClick={this.props.switchSkills}
          />
        </div>
      </div>
    );
  }
} // end of Portfolio Component

//Category buttons
function PortfolioMenu(props) {
  return (
    <div id="portfolio-menu" className="animated fadeInRight faster">
      <button
        className={props.currentCat === "featured" ? "selected" : "unselected"}
        onClick={props.switchCat}
        data-value="featured">
        Featured
      </button>
      <button
        className={props.currentCat === "front-end" ? "selected" : "unselected"}
        onClick={props.switchCat}
        data-value="front-end">
        Front End
      </button>
      <button
        className={props.currentCat === "full-stack" ? "selected" : "unselected"}
        onClick={props.switchCat}
        data-value="full-stack">
        Full Stack
      </button>
    </div>
  );
}

//Featured Full Card
class Featured extends Component {
  constructor(props) {
    super(props);
    this.state = {
      currentPic: 0
    };
    this.picRight = this.picRight.bind(this);
    this.picLeft = this.picLeft.bind(this);
  } // end constructor

  //Right Arrow Click - Switch pic
  picRight() {
    let newPic = this.state.currentPic + 1;
    this.setState({ currentPic: newPic });
  }

  //Right Arrow Click - Switch pic
  picLeft() {
    let newPic = this.state.currentPic - 1;
    this.setState({ currentPic: newPic });
  }
  render() {
    const project = featuredProject;
    const skills = project.skills.map(skill => (
      <h5 className="skill" key={skill}>
        {skill}
      </h5>
    ));
    const numPics = project.pics.length;

    return (
      <div className="featured-card animated fadeInLeft faster">
        <div className="card-pics">
          {" "}
          <img src={project.pics[this.state.currentPic]} alt="" />
          {this.state.currentPic > 0 && (
            <FontAwesomeIcon
              className="pic-left"
              icon={["fas", "arrow-left"]}
              size="3x"
              transform="shrink-5"
              onClick={this.picLeft}
            />
          )}
          {this.state.currentPic < numPics - 1 && (
            <FontAwesomeIcon
              className="pic-right"
              icon={["fas", "arrow-right"]}
              size="3x"
              transform="shrink-5"
              onClick={this.picRight}
            />
          )}
        </div>
        <div className="card-body">
          <h2 className="card-title">{project.title}</h2>
          <p className="card-description">{project.desc}</p>
          <div className="card-skills">{skills}</div> {/* end card-skills*/}
          <div className="card-footer">
            <a href={project.url} target="_blank" rel="noreferrer noopener">
              {" "}
              <div className="visit">
                <FontAwesomeIcon
                  className="visit-icon"
                  icon={["fas", "external-link-square-alt"]}
                  size="lg"
                />
                <h4 className="visit-text">View Site</h4>
              </div>
            </a>
          </div>{" "}
          {/* end card-footer */}
        </div>{" "}
        {/* end card-body */}
      </div>
    );
  }
}

//FrontEnd project preview cards
function FrontEnd(props) {
  const cards = frontEndProjects.map(project => (
    <div
      className="small-card"
      id={project.title}
      key={project.title}
      data-type="front-end"
      onClick={props.openModal}>
      <img src={project.pics[0]} alt="" />
      <h3 className="card-title">{project.title}</h3>
    </div>
  ));

  return (
    <div id="frontend-preview" className="preview-cards animated fadeInLeft faster">
      {cards}
    </div>
  );
}

//FullStack project preview cards
function FullStack(props) {
  const cards = fullStackProjects.map(project => (
    <div
      className="small-card"
      id={project.title}
      key={project.title}
      data-type="full-stack"
      onClick={props.openModal}>
      <img src={project.pics[0]} alt="" />
      <h3 className="card-title">{project.title}</h3>
    </div>
  ));

  return (
    <div id="fullstack-preview" className="preview-cards animated fadeInRight faster">
      {cards}
    </div>
  );
}

//Full card modal pop-up
class Modal extends Component {
  constructor(props) {
    super(props);
    this.state = {
      currentPic: 0
    };
    this.picRight = this.picRight.bind(this);
    this.picLeft = this.picLeft.bind(this);
  } // end constructor

  //Right Arrow Click - Switch pic
  picRight() {
    let newPic = this.state.currentPic + 1;
    this.setState({ currentPic: newPic });
  }

  //Right Arrow Click - Switch pic
  picLeft() {
    let newPic = this.state.currentPic - 1;
    this.setState({ currentPic: newPic });
  }

  render() {
    const skills = this.props.modalInfo.skills.map(skill => (
      <h5 className="skill" key={skill}>
        {skill}
      </h5>
    ));
    const numPics = this.props.modalInfo.pics.length;

    return (
      <div id="project-modal" onKeyDown={this.props.modalKeyDown}>
        <div className="full-card animated zoomIn faster">
          <div className="card-pics">
            {" "}
            <img src={this.props.modalInfo.pics[this.state.currentPic]} alt="" />
            {this.state.currentPic > 0 && (
              <FontAwesomeIcon
                className="pic-left"
                icon={["fas", "arrow-left"]}
                size="3x"
                transform="shrink-5"
                onClick={this.picLeft}
              />
            )}
            {this.state.currentPic < numPics - 1 && (
              <FontAwesomeIcon
                className="pic-right"
                icon={["fas", "arrow-right"]}
                size="3x"
                transform="shrink-5"
                onClick={this.picRight}
              />
            )}
          </div>
          <div className="card-body">
            <h2 className="card-title">{this.props.modalInfo.title}</h2>
            <p className="card-description">{this.props.modalInfo.desc}</p>
            <div className="card-skills">{skills}</div> {/* end card-skills*/}
            <div className="card-footer">
              <a href={this.props.modalInfo.url} target="_blank" rel="noreferrer noopener">
                {" "}
                <div className="visit">
                  <FontAwesomeIcon
                    className="visit-icon"
                    icon={["fas", "external-link-square-alt"]}
                    size="lg"
                  />
                  <h4 className="visit-text">View Site</h4>
                </div>
              </a>

              <div className="close">
                <button id="close-modal-button" onClick={this.props.closeModal}>
                  <FontAwesomeIcon
                    className="close-icon"
                    icon={["fas", "times"]}
                    size="3x"
                    transform="shrink-2 down-0.5"
                  />
                </button>
              </div>
            </div>{" "}
            {/* end card-footer */}
          </div>{" "}
          {/* end card-body */}
        </div>
      </div>
    );
  }
}

export default Portfolio;
